  height: 100%;
  left: 0px;
  position: absolute;
  top: 0px;
  width: 100%;
  z-index: 0;
  border-radius: 50%;
}

.video-btn:before {
  background-color: rgba(241, 44, 21, 0.1);
  content: "";
  height: calc(100% - 20px);
  left: 10px;
  position: absolute;
  top: 10px;
  width: calc(100% - 20px);
  z-index: 0;
  border-radius: 50%;
}

