  background-color: #D82E2A;
  color: #fff;
}

.menu ul li ul.dropdown-content li {
  padding: 0;
  display: block;
}

.menu ul li ul.dropdown-content li a {
  display: block;
  padding: 4px 0px;
  color: #161616;
  text-transform: capitalize;
  transition: all 0.35s ease 0s;
}

.menu > ul > li:hover > a,
.menu ul li.current > a {
